(function() {
  'use strict';

  angular
    .module('myPortfolio')
    .controller('WorkController', WorkCtrlFunction);

  /** @ngInject */
  function WorkCtrlFunction(AnimateService, WorkService, ProgressBarService) {
    var vm = this;

    vm.workViews = WorkService.fetch();

    vm.wobbleMe = function(id){
      var param='#'+id+'-image i';
      AnimateService.animateCss('wobble',param);
    }

    ProgressBarService.stop();

  }
})();

(function() {
  'use strict';

  angular
    .module('myPortfolio')
    .controller('HomeController', HomeCtrlFunction);

  /** @ngInject */
  function HomeCtrlFunction(HomeService, ProgressBarService, AnimateService, $interval, $scope) {
    var vm = this;
    var interval;

    var data = HomeService.fetch();
    vm.eatthis=data.me[data.iterator];
    interval = $interval(function(){
      if(data.iterator===data.me.length-1)
        data.iterator=0;
      else
        data.iterator++;

      vm.eatthis=data.me[data.iterator];
      AnimateService.animateCss('fadeIn','.thisisme');
    },3000);

    $scope.$on('$destroy',function(){
      if(interval)
        $interval.cancel(interval);
    });

    ProgressBarService.stop();
  }
})();

